import React from 'react';
import ReactDom from 'react-dom';
import Dropdown from '../index';

const DropdownSearchableDemo = function () {
  let options = [
    {
      name: 'swift',
      value: 101
    }, {
      name: 'java',
      value: 121
    }, {
      name: 'javascript',
      value: 332
    }
  ];
  let bigOptions = [];
  for (let i = 0; i < 100; i++) {
    bigOptions.push({
      name: `name${i}`,
      value: i
    });
  }
  return (
    <div>
      <h3>with default selected</h3>
      <Dropdown
        options={options}
        defaultOption={options[0]}
        searchable
      />
      <h3>without default selected</h3>
      <Dropdown
        options={options}
        searchable
      />
      <h3>big data</h3>
      <Dropdown
        options={bigOptions}
        searchable
      />
    </div>
  );
};

ReactDom.render(<DropdownSearchableDemo />, document.getElementById('searchable'));
